  <div class="router-main login-main">
    <div class="login">
      <h2>
        ÂÖçË¥πÊ≥®ÂÜå
        <span></span>
      </h2>
      <el-form class="loginform" :model="form" :rules="rules" label-width="70px" ref="form">
        <el-form-item label="ÊâãÊú∫Âè∑" prop="username">
          <el-input placeholder="ËØ•ÊâãÊú∫Âè∑‰∏∫ÊâæÂõûÂØÜÁ†ÅÂîØ‰∏ÄÈÄîÂæÑ" v-model.trim="form.username"></el-input>
        </el-form-item>
        <el-form-item label="ÂØÜÁ†Å" prop="password">
          <el-input type="password" placeholder="ÂØÜÁ†Å" v-model.trim="form.password"></el-input>
        </el-form-item>
        <el-form-item label="ÈáçÂ§çÂØÜÁ†Å" prop="repassword">
          <el-input type="password" placeholder="ÂØÜÁ†Å" v-model.trim="form.repassword"></el-input>
        </el-form-item>
        <el-form-item label="È™åËØÅÁ†Å" prop="code" class="code">
          <el-input placeholder="ËØ∑ËæìÂÖ•È™åËØÅÁ†Å" v-model.trim="form.code"></el-input>
          <div class="img" v-html="src" @click="setSrc"></div>
        </el-form-item>
        <el-form-item>
          <el-button type="success" size="medium" style="width:100%;" @click="submitHandle">Ê≥®ÂÜå</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
