<script>
export default {
  data() {
    return {
      src: '',
      form: {
        username: '',
        password: '',
        repassword: '',
        code: ''
      },
      rules: {
        username: [
          {
            validator: this.validateUsername,
            trigger: 'blur',
            message: 'ËØ∑Â°´ÂÜôÊÇ®ÁöÑÊâãÊú∫Âè∑'
          }
        ],
        password: [
          {
            validator: this.validatePassword,
            trigger: 'blur',
            message: 'ËØ∑Â°´ÂÜô6-20‰ΩçÁöÑÂØÜÁ†Å'
          }
        ],
        repassword: [
          {
            validator: this.validateRePassword,
            trigger: 'blur',
            message: '‰∏§Ê¨°ÂØÜÁ†Å‰∏ç‰∏ÄËá¥'
          }
        ],
        code: [
          {
            validator: this.validateCode,
            message: 'ËØ∑Â°´ÂÜô4‰ΩçÁöÑÈ™åËØÅÁ†Å',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  methods: {
    submitNative() {
      return false;
    },
    validateUsername(rule, value, callback) {
      const len = value.length;
      if (len != 11) {
        callback(new Error('ËØ∑Â°´ÂÜôÊÇ®ÁöÑÊâãÊú∫Âè∑'));
      } else {
        callback();
      }
    },
    validatePassword(rule, value, callback) {
      const len = value.length;
      if (len > 20 || len < 6) {
        callback(new Error('ËØ∑Â°´ÂÜôÂØÜÁ†Å'));
      } else {
        callback();
      }
    },
    validateRePassword(rule, value, callback) {
      const len = value.length;
      if (len > 20 || len < 6) {
        callback(new Error('ËØ∑Â°´ÂÜôÂØÜÁ†Å'));
      } else if (value !== this.form.password) {
        callback(new Error('‰∏§Ê¨°ÂØÜÁ†Å‰∏ç‰∏ÄËá¥'));
      } else {
        callback();
      }
    },
    validateCode(rule, value, callback) {
      const len = value.length;
      if (len == 0 || len > 4) {
        callback(new Error('ËØ∑Â°´‰ΩçÈ™åËØÅÁ†Å'));
      } else {
        callback();
      }
    },
    async setSrc() {
      const doc = await this.$tools.httpAgent('/plugin/captcha', 'get', {
        1: 1
      });
      this.src = doc.data.captcha;
      localStorage.setItem('captcha', doc.data.captchaid);
    },
    submitHandle: async function () {
      // const self = this;
      this.$refs.form.validate(async valid => {
        if (valid) {
          this.form.captcha = localStorage.getItem('captcha');
          const doc = await this.$tools.httpAgent('/reg', 'post', this.form, {
            login: false
          });
          if (doc.code === 200) {
            localStorage.setItem('jwt', JSON.stringify(doc.data));
            this.$router.push('/usercenter');
          } else {
            // self.$layer.msg('Â∏êÂè∑ÊàñËÄÖÂØÜÁ†ÅÈîôËØØ');
            this.setSrc();
          }
        }
      });
    }
  },
  mounted() {
    this.setSrc();
  }
}
</script>

